import React, { useEffect, useState, useRef } from "react";
import Highcharts from "highcharts/highstock";
import HighchartsReact from "highcharts-react-official";
import "./chart.css";

const GPChart = (props) => {
  const [chartData, setChartData] = useState(null);
  const [chartTitle, setChartTitle] = useState("");

  let chart = useRef(null);
  useEffect(() => {
    chart = chart.current.chart;
  });

  useEffect(() => {
    setChartData([props.data]);
    setChartTitle(props.data.name);
    chart.xAxis[0].setExtremes(
      props.data.data[0] ? props.data.data[0][0] : 0,
      props.data.data[0] ? props.data.data[props.data.data.length - 1][0] : 0
    );
  }, [props.data]);

  const options = {
    chart: {
      type: "column",
      // margin: 0,
      // width: 1000,
    },
    title: {
      text: props.data.name,
    },
    series: [
      {
        name: props.data.name,
        data: props.data.data,
      },
    ],
    rangeSelector: {
      enabled: true,
      buttons: [],
      verticalAlign: "top",
      buttonPosition: {
        align: "right",
      },
      inputPosition: {
        align: "left",
      },
    },
    xAxis: {
      type: "datetime",
      dateTimeLabelFormats: {
        day: "%y-%b-%e",
      },
      //categories: props.data.xcategories,
    },
    /*navigator: {
      enabled: false,
    },*/
    colors: ["#00A6D6", "#002E54"],
    plotOptions: {
      series: {
        point: {
          events: {
            mouseOver: function () {
              const point = { x: this.x, y: this.y };
              console.log(point);
              props.setPoint([
                new Date(point.x).toISOString().split("T")[0],
                //props.data.xcategories[point.x],
                point.y,
              ]);
            },
          },
        },
      },
    },
  };

  return (
    <div>
      <HighchartsReact
        highcharts={Highcharts}
        constructorType={"stockChart"}
        options={options}
        ref={chart}
      />
    </div>
  );
};

export default React.memo(GPChart);
