import React, {useEffect, useState} from "react";
import EnergyCard from "./EnergyCard";
import "../../styles/common.css";
import axios from "axios";

const EnergyCards = () => {

  const [currentPower, setCurrentPower] = useState(null);
  const [energyToday, setEnergyToday] = useState(null);
  const [energyMonth, setEnergyMonth] = useState(null);
  const [energyLifeTime, setEnergyLifeTime] = useState(null);

  useEffect(() => {
    const getCurrentPowerData = async () => {
      try {
        let url = "/api/sys/currentpower/1"
        const response = await axios.get(url);
        setCurrentPower(response.data.power + "W");
      } catch (e) {
        console.log(e);
      }
    };
    const getEnergyToday = async () => {
      try {
        let url = "/api/sys/energytoday/1/1588431600000"
        const response = await axios.get(url);
        setEnergyToday(parseInt(response.data.totalwh/1000) + "kWh")
      } catch (e) {
        console.log(e);
      }
    }

    const getEnergyMonth = async () => {
      try {
        let url = "/api/sys/energymonth/1/1588431600000"
        const response = await axios.get(url);
        setEnergyMonth(parseInt(response.data.totalwh/1000) + "kWh")
      } catch (e) {
        console.log(e);
      }
    }

    const getEnergyLiftTime = async () => {
      try {
        let url = "/api/sys/energylifetime/1"
        const response = await axios.get(url);
        setEnergyLifeTime(parseInt(response.data.totalwh/1000) + "kWh")
      } catch (e) {
        console.log(e);
      }
    }

    getCurrentPowerData();
    getEnergyToday();
    getEnergyMonth();
    getEnergyLiftTime();
  }, [])

  const cards = [
    { id: 0, name: "Current Power", value: currentPower != null ? currentPower : "0W"} ,
    { id: 1, name: "Energy Today", value: energyToday != null ? energyToday : "0Wh" },
    { id: 2, name: "Energy Month", value: energyMonth != null ? energyMonth : "0Wh" },
    { id: 3, name: "Lifetime Energy", value: energyLifeTime != null ? energyLifeTime : "0Wh" },
  ];

  return (
    <div className="d-flex justify-content-center m-5 flex-wrap">
      {cards.map((card) => (
        <EnergyCard card={card} key={card.id} />
      ))}
    </div>
  );
};

export default EnergyCards;
