import React, { useState, useEffect } from "react";
import Highcharts from "highcharts";
import GPChart from "./GPChart";

import {
  TabContent,
  TabPane,
  Nav,
  NavItem,
  NavLink,
  Row,
  Col,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap";
import classnames from "classnames";

const GraphDetail = (props) => {
  const [curPoint, setPoint] = useState(["", 0]);
  const [activeTab, setActiveTab] = useState(1);

  const toggle = (tab) => {
    if (activeTab !== tab) setActiveTab(tab);
  };

  const unitToggle = (evt) => {
    evt.preventDefault();
    props.setTimeRange(evt.target.innerText);
  };

  return (
    <div className="card shadow mb-4">
      <div className="card-header py-2">Graph</div>
      <div className="card-body">
        {/*<div>
          <Breadcrumb tag="nav" listTag="div">
            {props.timeRange === "day" ? (
              <BreadcrumbItem active onClick={unitToggle}>
                day
              </BreadcrumbItem>
            ) : (
              <BreadcrumbItem onClick={unitToggle}>day</BreadcrumbItem>
            )}
            {props.timeRange === "week" ? (
              <BreadcrumbItem active onClick={unitToggle}>
                week
              </BreadcrumbItem>
            ) : (
              <BreadcrumbItem onClick={unitToggle}>week</BreadcrumbItem>
            )}
            {props.timeRange === "month" ? (
              <BreadcrumbItem active onClick={unitToggle}>
                month
              </BreadcrumbItem>
            ) : (
              <BreadcrumbItem onClick={unitToggle}>month</BreadcrumbItem>
            )}
          </Breadcrumb>
            </div>*/}
        <Nav tabs>
          {props.data != null &&
            Array.from(props.data).map((d, idx) => {
              return (
                <NavItem key={idx + 1}>
                  <NavLink
                    className={classnames({ active: activeTab === idx + 1 })}
                    onClick={() => {
                      toggle(idx + 1);
                    }}
                  >
                    {d.name}
                  </NavLink>
                </NavItem>
              );
            })}
        </Nav>
        <TabContent activeTab={activeTab}>
          {props.data != null &&
            Array.from(props.data).map((d, idx) => {
              return (
                <TabPane tabId={idx + 1} key={idx + 1}>
                  <Row>
                    <Col sm="12">
                      <div className="d-flex chartWrapper">
                        <GPChart
                          highcharts={Highcharts}
                          setPoint={setPoint}
                          data={d}
                          selectedItemType={props.selectedItemType}
                        ></GPChart>
                        <p>
                          Time is:{" "}
                          {curPoint[0] !== ""
                            ? curPoint[0]
                            : new Date().toDateString()}{" "}
                          and power is: {curPoint[1]}
                        </p>
                      </div>
                    </Col>
                  </Row>
                </TabPane>
              );
            })}
        </TabContent>
      </div>
    </div>
  );
};

export default GraphDetail;
